import Product from "./Product";
const Products = (props) => {
    
    let products = props.products;
    let addProduct = props.addProduct;
    let formDisplay = {};
    if (addProduct === false) {
        formDisplay = {
            display: 'none'
        }
    }else{
        formDisplay = {
            display: 'block'
        }
    }

    return ( 
        <div class="produits mb-5">
                <button onClick={props.onToggleAddProduct} type='submit' 
                class="my-2 btn btn-primary float-end">{addProduct === false ? "Ajouter une produit" : "Fermer"}</button>
                <h2>Boutique</h2>
                
                <div style={formDisplay} class="boutiqueForm col-12">
                        <form class=" col-12  g-3">
                        <div class="productForm col">
                            <label for="inputEmail4" class="form-label">Nom du produit</label>
                            <input type="text" class="form-control" id="inputEmail4" />
                        </div>
                        <div class="productForm col">
                            <label for="inputEmail4" class="form-label">Genre</label>
                            <input type="text" class="form-control" id="inputEmail4" />
                        </div>
                        <div class="productForm col mb-3">
                            <label for="exampleFormControlTextarea1" class="form-label">Descriptif</label>
                            <textarea class="form-control" id="exampleFormControlTextarea1" rows="3" ></textarea>
                        </div>
                        <label class="form-check-label" >Tailles</label>
                        <div class="tailles col px-4 mb-2s d-flex align-items-center">
                            
                            <div class="px-4 form-check form-switch">
                            <input class="  form-check-input " type="checkbox" id="flexSwitchCheckDefault"/>
                            <label class="form-check-label" for="flexSwitchCheckDefault">S</label>
                            </div>
                            <div class="px-4 form-check form-switch">
                            <input class="  form-check-input " type="checkbox" id="flexSwitchCheckChecked" />
                            <label class="form-check-label" for="flexSwitchCheckChecked">M</label>
                            </div>
                            <div class="px-4 form-check form-switch">
                            <input class="  form-check-input " type="checkbox" id="flexSwitchCheckDisabled" />
                            <label class="form-check-label" for="flexSwitchCheckDisabled">L</label>
                            </div>
                            <div class="px-4 form-check form-switch">
                            <input class="  form-check-input " type="checkbox" id="flexSwitchCheckCheckedDisabled"/>
                            <label class="form-check-label" for="flexSwitchCheckCheckedDisabled">XL</label>
                            
                        </div>
                        </div>
                        
                        <div class="productForm col">
                            <label for="inputEmail4" class="form-label">Prix</label>
                            <input type="number" class="form-control" id="inputEmail4" />
                        </div>
                        <div class="productForm col">
                            <label for="inputEmail4" class="form-label">Lien</label>
                            <input type="text" class="form-control" id="inputEmail4" />
                        </div>
                        <label class="my-2" for="inputGroupFile01">Image</label>
                        <div class="productForm input-group mb-3">
                            <label class="input-group-text pt-2 fas fa-upload" for="inputGroupFile01"></label>
                            <input onChange={props.uploadFile} type="file" class="form-control" id="inputGroupFile01" required/>
                        </div>
                        <div className="bottom-card">
                        <div class="col-12">
                        <button onClick={props.onToggleSubmitProduct} type="submit" class="btn btn-primary">Publier</button>
                    </div>
                        </div>
                    </form>
                </div>
                <div class="d-flex col-12 justify-content-between flex-wrap flex-md-nowrap align-items-center pt-3 pb-2 mb-3 border-bottom">
                    <div class="card-group  produitCards col-12">
                        {products.map((product, index) => {
                            return <Product 
                            uploadFile={props.uploadFile}
                            onToggleUpdateProduct={props.onToggleUpdateProduct}
                            onToggleDeleteProduct={props.onToggleDeleteProduct}
                            key={index} {...product} index={index}/>
                        })}
                    </div>

                </div>
        </div>
     );
}
 
export default Products;