
const Experience = (props) => {
    let mailto = 'mailto:'+ props.email;
    let buttonDisplay = {};
    let certificateDisplay = {};
    if (props.isValidated === false) {
        buttonDisplay = {
            display : 'block'
        };
        certificateDisplay = {
            display : 'none'
        };
    }else{
        buttonDisplay = {
            display : 'none'
        };
        certificateDisplay = {
            display : 'inline-block',
            color : 'rgb(100, 189, 10)'
        };
    }
    return ( 
        <div className=" col-md-6 col-sm-12">
            <div class="card col-12">
            <div class="card-body">
                <h6 class="card-subtitle mb-2 text-muted float-end">{props.date} à {props.hour}</h6>
                <h6 class="card-title text-muted"><a href={mailto}
                 class="card-link">{props.email}</a></h6>
                <h5 class="card-subtitle mb-2 text-muted">Titre : {props.subject}</h5>
                
                <p class="card-text">{props.text}</p>
                <button style={buttonDisplay} onClick={(event) => {
                    props.onToggleValidateExperience(event, parseInt(props.index))
                }} type='submit' class="btn btn-primary float-end">Valider</button>
                 <button onClick={() => {
                    props.onToggleDeleteExperience(parseInt(props.index))
                }} type='submit' class="btn btn-outline-danger mx-2 float-end">Effacer</button>
                <p class="card-text text-muted"><i style={certificateDisplay} class="fas fa-certificate"></i> {props.isValidated === false ? 'En attente de validation...' : 'Validée'}</p>
                
            </div>
            </div>
        </div>
     );
}
 
export default Experience;