const Product = (props) => {
    let sizes = props.size;

    let availableSizes = [];
    
    for (const size in sizes) {
        if (sizes[size] === true) {
            availableSizes.push(size);
        }
    }
    let listOfSizes = '';
    for (let i = 0; i < availableSizes.length; i++) {
        if (i === 0) {
            listOfSizes += 'Tailles disponibles : ' + availableSizes[i];
        }else{
            listOfSizes += ' / ' + availableSizes[i]; 
        }
        
    }
    let produit = '';
    if (props.isEditing === false) {
        produit = (
                    <div class="card  product">
                        <img src={props.img} class="card-img-top" alt="..."/>
                        <div class="card-body">
                        <h2 class="card-title">{props.name}</h2>
                        <h5 class="card-title">{props.type}</h5>
                        <p class="card-text text">{props.description}</p>
                        <div className="bottom-card">
                            <p class="card-text"><small class="text-muted">{listOfSizes}</small></p>
                            <p class="card-text">{props.price} euros</p>
                            <p class="card-text">Lien de vente : {props.link}</p>
                            <div className="bottom-card">
                                <button onClick={() => {props.onToggleUpdateProduct(props.index)
                                }} type="button" class="fas fa-edit btn btn-dark"></button>
                                <button onClick={() => {props.onToggleDeleteProduct(props.index)
                                }} type="button" class="fa fa-trash btn btn-danger"></button>
                            </div>
                            
                        </div>
                        </div>
                                        
                    </div>
                );
    }else{
        produit = (
                    <div class=" product card">
                        <img src={props.img} class="card-img-top" alt="..."/>
                        <form class="updateBoutiqueForm col  g-3">
                        <div class="col updateProductForm">
                            <label for="inputEmail4" class="form-label">Nom du produit</label>
                            <input type="text" class="form-control" id="inputEmail4" defaultValue={props.name}/>
                        </div>
                        <div class="col updateProductForm">
                            <label for="inputEmail4" class="form-label">Genre</label>
                            <input type="text" class="form-control" id="inputEmail4" defaultValue={props.type}/>
                        </div>
                        <div class="col updateProductForm mb-3">
                            <label for="exampleFormControlTextarea1" class="form-label">Descriptif</label>
                            <textarea class="form-control" id="exampleFormControlTextarea1" rows="3" defaultValue={props.description}></textarea>
                        </div>
                        <label class="form-check-label" >Tailles</label>
                        <div class="col px-4 mb-2s d-flex align-items-center tailles">
                            
                            <div class="px-4 form-check form-switch">
                            <label class="form-check-label" for="flexSwitchCheckDefault">S</label>
                            <input class="  form-check-input " type="checkbox" id="flexSwitchCheckDefault"  defaultChecked={sizes['S'] === true ? true : false }/>
                            </div>
                            <div class="px-4 form-check form-switch">
                            <label class="form-check-label" for="flexSwitchCheckChecked">M</label>
                            <input class="  form-check-input " type="checkbox" id="flexSwitchCheckChecked" defaultChecked={sizes['M'] === true ? true : false }/>
                           
                            </div>
                            <div class="px-4 form-check form-switch">
                            <label class="form-check-label" for="flexSwitchCheckDisabled">L</label>
                            <input class="  form-check-input " type="checkbox" id="flexSwitchCheckDisabled"  defaultChecked={sizes['L'] === true ? true : false }/>
                            </div>
                            <div class="px-4 form-check form-switch">
                            <label class="form-check-label" for="flexSwitchCheckCheckedDisabled">XL</label>
                            <input class="  form-check-input " type="checkbox" id="flexSwitchCheckCheckedDisabled" defaultChecked={sizes['XL'] === true ? true : false }/>
                            
                            
                        </div>
                        </div>
                        
                        <div class="col updateProductForm">
                            <label for="inputEmail4" class="form-label">Prix</label>
                            <input type="text" class="form-control" id="inputEmail4" defaultValue={props.price}/>
                        </div>
                        <div class="col updateProductForm">
                            <label for="inputEmail4" class="form-label">Lien</label>
                            <input type="text" class="form-control" id="inputEmail4" defaultValue={props.link}/>
                        </div>
                        <label class="my-2" for="inputGroupFile01">Image</label>
                        <div class="input-group updateProductForm mb-3">
                            <label class="input-group-text pt-2 fas fa-upload" for="inputGroupFile01"></label>
                            <input onChange={props.uploadFile} type="file" class="form-control" id="inputGroupFile01" required/>
                        </div>
                        <div className="bottom-card">
                        <button onClick={() => {props.onToggleUpdateProduct(props.index)
                        }} type="button" class="fas fa-edit btn btn-dark"></button>
                        <button onClick={() => {props.onToggleDeleteProduct(props.index)
                        }} type="button" class="fa fa-trash btn btn-danger"></button>
                        </div>
                    </form>
                </div>
            );
    }
    return ( 
        <div className="col-md-4 col-sm-6 mb-3">
           {produit} 
                    
        </div>
     );
}
 
export default Product;