import Admin from "./Admin";
const Admins = (props) => {
  
    let admins = props.admins;
    let formStyle ={};
    if (props.addAdmin === false) {
      formStyle = {
        display: 'none'
      }
    }else{
      formStyle = {
        display: 'block'
      }
    }
    return ( 
        <div class="administrateurs">
                <h2>Administrateurs</h2>
                <button type='submit' onClick={props.onToggleAddAdmin} 
                  class="btn btn-primary float-end">{props.addAdmin === false ? 'Ajouter un administrateur' : 'Fermer'}</button>
                  <div style={formStyle} className="adminFormContainer  col-12">
                  <form className='addAdmin  col-12' action="">
                    <div class="row">
                        <div class="col">
                          <input type="text" class="name form-control" placeholder="Nom" aria-label="First name"  required/>
                        </div>
                        <div class="col">
                          <input type="text" class="poste form-control" placeholder="Poste" aria-label="Last name" required/>
                        </div>
                        <div class="col">
                            <input type="text" class="email form-control" placeholder="Email" aria-label="Last name" required/>
                          </div>
                      </div>
                      <button onClick={props.onToggleSubmitAdmin} class="closeForm m-2 btn btn-primary">Ajouter</button>
                  </form>
                  </div>
                <table class="table">
                    <thead>
                      <tr>
                        <th scope="col">#</th>
                        <th scope="col">Nom</th>
                        <th scope="col">Poste</th>
                        <th scope="col">Email</th>
                      </tr>
                    </thead>
                    <tbody class="table">
                        {
                        admins.map((admin, index) => {
                           return <Admin key={index} index={index} {...admin}
                           onToggleDeleteAdmin={props.onToggleDeleteAdmin}
                           onToggleUpdateAdmin={props.onToggleUpdateAdmin}/>
                        })}
                    </tbody>
                  </table>
                  
                  
            </div>
     );
}
 
export default Admins;