const onToggleAddAdmin = (event) => {
  event.preventDefault();
  setAddAdmin(!addAdmin);
  let inputs = document.querySelectorAll('.addAdmin input');  
  for (let i = 0; i < inputs.length; i++) {
    inputs[i].value = '';;
  }
}
const onToggleSubmitAdmin = (event) => {
  event.preventDefault();
  let inputs = document.querySelectorAll('.addAdmin input');
    if (inputs[0].value && inputs[1].value && inputs[2].value) {
      
      let newAdminInfo = {
      name: inputs[0].value,
      poste: inputs[1].value,
      email : inputs[2].value,
      isEditing : false,
      
      }
      for (let i = 0; i < inputs.length; i++) {
        inputs[i].value = '';
      }
      pushData('admins', newAdminInfo);
      setAddAdmin(!addAdmin);
      
      Toast.fire({
        icon: 'success',
        title: 'Ajouté avec succès'
      })
    }else{
        swal('Attention', "Les champs n'ont pas été correctement saisis");
    }
    setAddAdmin(!addAdmin);
} 
const onToggleUpdateAdmin = (index) => {
  setAdmins(admins.map((admin)=> {
    if (index === admins.indexOf(admin)) {
      if (admins[index].isEditing === false) {
        updateData('admins', admin, {isEditing : true});
        admins[index].isEditing = true;
      }else{
        
        let inputs = document.querySelectorAll('td input');
        let updateAdmin = {
          isEditing:false,
          id : admin.id,
          name: inputs[0].value.trim(),
          poste: inputs[1].value.trim(),
          email: inputs[2].value.trim(),
        };
        updateData('admins', admin, updateAdmin);
        admin = updateAdmin;
        
        Toast.fire({
          icon: 'success',
          title: 'Mise à jour avec succès'
        })
      }
    }
    return admin;
  }));
  
}
const onToggleDeleteAdmin = (index) => {
  if (admins.length > 1) {
      setAdmins(admins.filter((admin)=> {
      if (admins.length >= 1 && index !== admins.indexOf(admin)) {
        return admin;
      }else{
        deleteData('admins', admin);
        Toast.fire({
          icon: 'warning',
          title: 'Supprimé avec succès'
        })
      }
      
      
    }));
  }else{
    swal('Attention', "Il doit y avoir au moins un administrateur");
  }
  
}
