import Admins from "./Admins/Admins";
import Articles from "./Articles/Articles";
import Dates from "./Dates/Dates";
import Products from "./Products/Products";
import Messages from "./Messages/Messages";
import Experiences from "./Experiences/Experiences";
const Dashboard = (props) => {
  let componentToDisplay;
  let displayDashboard = props.displayDashboard;
  if (displayDashboard.administrateurs === true) {
    componentToDisplay = <Admins
    onToggleDeleteAdmin={props.onToggleDeleteAdmin}
    onToggleAddAdmin={props.onToggleAddAdmin}
    onToggleSubmitAdmin={props.onToggleSubmitAdmin} 
    addAdmin={props.addAdmin} 
    onToggleUpdateAdmin={props.onToggleUpdateAdmin}
    admins={props.admins}/>
  }
  if (displayDashboard.publications === true) {
    componentToDisplay = <Articles
    onToggleDeleteArticle={props.onToggleDeleteArticle}
    onToggleUpdateArticle={props.onToggleUpdateArticle}
    uploadFile={props.uploadFile}
    isAddArticle={props.isAddArticle}
    onToggleSubmitArticle={props.onToggleSubmitArticle}
    onToggleAddArticle={props.onToggleAddArticle}
    articles={props.articles}/>
  }
  if (displayDashboard.dates === true) {
    componentToDisplay = <Dates 
    onToggleUpdateDate={props.onToggleUpdateDate}
    onToggleDeleteDate={props.onToggleDeleteDate}
    dates={props.dates}
    onToggleSubmitDate={props.onToggleSubmitDate}
    onToggleAddDate={props.onToggleAddDate}
    addDate={props.addDate} />
  }
  if (displayDashboard.boutique === true) {
    componentToDisplay = <Products
    onToggleUpdateProduct={props.onToggleUpdateProduct}
    onToggleDeleteProduct={props.onToggleDeleteProduct}
    products={props.products}
    uploadFile={props.uploadFile}
    onToggleSubmitProduct={props.onToggleSubmitProduct}
    onToggleAddProduct={props.onToggleAddProduct}
    addProduct={props.addProduct}/>
  }
  if (displayDashboard.experiences === true) {
    componentToDisplay = <Experiences
    experiences={props.experiences}
    onToggleValidateExperience={props.onToggleValidateExperience}
    onToggleDeleteExperience={props.onToggleDeleteExperience}
    />
  }
  if (displayDashboard.messages === true) {
    componentToDisplay = <Messages
    onToggleDeleteMessage={props.onToggleDeleteMessage}
    messages={props.messages}
    />
  }
    return ( 
        
<div>
      <header class="navbar navbar-dark sticky-top bg-dark flex-md-nowrap p-0 shadow">
        <a class="navbar-brand col-md-3 col-lg-2 me-0 px-3" href="#">Espace d'administration - LANA DEL REY</a>
        <button class="navbar-toggler position-absolute d-md-none collapsed" type="button" data-bs-toggle="collapse" data-bs-target="#sidebarMenu" aria-controls="sidebarMenu" aria-expanded="false" aria-label="Toggle navigation">
          <span class="navbar-toggler-icon"></span>
        </button>
        
        <nav class="navbar navbar-light bg-dark">
            <div class="container-fluid user">
              <span class="navbar-brand mb-0 h1"></span>
                <img src="" alt="user"/>
            </div>
        </nav>
        <ul class="navbar-nav px-3">
          <li class="nav-item text-nowrap">
            <a onClick={props.onToggleLogout} class="nav-link logout" href="#">Se déconnecter</a>
          </li>
          
        </ul>
      </header>       
    <div class="container-fluid">
        <div class="row">
          <nav id="sidebarMenu" class="col-md-3 col-lg-2 d-md-block position-fixed bg-light sidebar collapse vh-100">
            <div class="position-sticky pt-3">
              <ul class="nav flex-column">
                <li class="nav-item">
                  <a onClick={() => {
                    props.setDisplayDashboard({
                      administrateurs : true,
                      publications : false,
                      dates : false,
                      boutique : false,
                      experiences : false,
                      messages : false
                    })
                  }} class="admins nav-link active" aria-current="page" href="#">
                    <span data-feather="home"></span>
                    Administrateurs
                  </a>
                </li>
                <li class="nav-item">
                  <a onClick={() => {
                    props.setDisplayDashboard({
                      administrateurs : false,
                      publications : true,
                      dates : false,
                      boutique : false,
                      experiences : false,
                      messages : false
                    })
                  }} class="publications nav-link" href="#">
                    <span data-feather="file"></span>
                    Publications
                  </a>
                </li>
                <li class="nav-item">
                  <a onClick={() => {
                    props.setDisplayDashboard({
                      administrateurs : false,
                      publications : false,
                      dates : true,
                      boutique : false,
                      experiences : false,
                      messages : false
                    })
                  }} class="dates nav-link" href="#">
                    <span data-feather="shopping-cart"></span>
                    Dates
                  </a>
                </li>
                <li class="nav-item">
                  <a onClick={() => {
                    props.setDisplayDashboard({
                      administrateurs : false,
                      publications : false,
                      dates : false,
                      boutique : true,
                      experiences : false,
                      messages : false
                    })
                  }} class="boutique nav-link" href="#">
                    <span data-feather="users"></span>
                    Boutique
                  </a>
                </li>
                <li class="comments nav-item">
                  <a  onClick={() => {
                    props.setDisplayDashboard({
                      administrateurs : false,
                      publications : false,
                      dates : false,
                      boutique : false,
                      experiences : true,
                      messages : false
                    })
                  }} class="nav-link" href="#">
                    <span data-feather="layers"></span>
                    Experiences
                  </a>
                </li>
                <li class="nav-item">
                  <a onClick={() => {
                    props.setDisplayDashboard({
                      administrateurs : false,
                      publications : false,
                      dates : false,
                      boutique : false,
                      experiences : false,
                      messages : true
                    })
                  }} class="messages nav-link" href="#">
                    <span data-feather="bar-chart-2"></span>
                    Messages
                  </a>
                </li>

              </ul>
      
              
            </div>
          </nav>
      
          <main class="col-md-9 ms-sm-auto col-lg-10 px-md-4">
            
            {componentToDisplay}
          </main>
        </div>
      </div>
    </div>
     );
}
 
export default Dashboard;