const DateTour = (props) => {
    const options = {year: 'numeric', month: 'numeric', day: 'numeric' };
    let date = props.date;
    let dateTour = new Date(date).toLocaleDateString('fr-FR', options);
    let isAvailableDateColor = "";
    let isAvailableDate = {};
    let isAvailable = props.isAvailable;
    if (isAvailable === true) {
        isAvailableDateColor = {
            color : "green"
        };
        isAvailableDate = "Disponible";
    }else{
        isAvailableDateColor = {
            color : "red"
        };
        isAvailableDate = "Complet";
    }

    let dateInfo = '';
    if (props.isEditing === false) {
        dateInfo = (<tr>
                <th  scope="row">{props.index + 1}</th>
                <td>{dateTour}</td>
                <td>{props.continent}</td>
                <td>{props.country}</td>
                <td>{props.city}</td>
                <td>{props.place}</td>
                <td style={isAvailableDateColor}>{isAvailableDate}</td>
                <td>{props.link}</td>
                <td>
                <button onClick={() => {
                    props.onToggleUpdateDate(parseInt(props.index));
                }} type="button" class="fas fa-edit btn btn-outline-dark"></button>
                <button  onClick={() => {
                    props.onToggleDeleteDate(parseInt(props.index));
                }} type="button" class="fa fa-trash btn btn-outline-danger"></button>
                </td>
            </tr>);
    }
    if (props.isEditing === true){
        dateInfo = (
            <tr className='dateTable'>
                <th  scope="row">{props.index + 1}</th>
                <td>
                    <input class="form-control" type="date" id="example-date-input" defaultValue={props.date}/>
                </td>
                <td className="continentSelect">
                    <select class="form-select " aria-label="Default select example" defaultValue={props.continent}>
                        <option value="USA">USA</option>
                        <option value="Europe">Europe</option>
                    </select>   
                </td>
                <td>
                    <input type="text" class="form-control" id="inputEmail4" defaultValue={props.country}/>    
                </td>
                <td>
                    <input type="text" class="form-control" id="inputEmail4" defaultValue={props.city}/>    
                </td>
                <td>
                    <input type="text" class="form-control" id="inputEmail4" defaultValue={props.place}/>    
                </td>
                <td className="avaibilitySelect">
                    <select class="form-select " aria-label="Default select example" >
                            <option value="true">Disponible</option>
                            <option value="false">Complet</option>
                        </select> 
                </td>
                
                <td>
                    <input type="text" class="form-control" id="inputEmail4" defaultValue={props.link}/>
                </td>
                <td>
                <button onClick={() => {
                    props.onToggleUpdateDate(parseInt(props.index));
                }} type="button" class="fas fa-edit btn btn-outline-dark"></button>
                <button  onClick={() => {
                    props.onToggleDeleteDate(parseInt(props.index));
                }} type="button" class="fa fa-trash btn btn-outline-danger"></button>
                </td>
            </tr>
        );
    }
    return ( 
        <>
            {dateInfo}
        </>
     );
}
 
export default DateTour;