import Message from "./Message";

const Messages = (props) => {
    let messages = props.messages;
    return ( 
        <div class="produits mb-5">
                <h2>Messages</h2>
                <div class="d-flex col-12 justify-content-between flex-wrap flex-md-nowrap align-items-center pt-3 pb-2 mb-3 border-bottom">
                    <div class="card-group messagesCards col-12">
                        {messages.map((message, index) => {
                            return <Message key={index} 
                            {...message}
                            index={index}
                            onToggleDeleteMessage={props.onToggleDeleteMessage}
                            />
                        })}
                    </div>
                </div>
        </div>
     );
}
 
export default Messages;