import DateTour from "./DateTour";
const Dates = (props) => {
    let dates = props.dates;
    dates.sort((a, b) => {
        if ( a.date < b.date ){
            return -1;
          }
          if ( a.date > b.date ){
            return 1;
          }
          return 0;
    })

    let addDate = props.addDate;
    let formDisplay = {};
    if (addDate === false) {
        formDisplay = {
            display: 'none'
        }
    }else{
        formDisplay = {
            display: 'block'
        }
    }
    return ( 
        <div class="Dates col-12">
            <button type='submit' onClick={props.onToggleAddDate} 
                  class="my-2 btn btn-primary float-end">{addDate == false ? "Ajouter une date" : "Fermer"}</button>
                <h2>Dates</h2>
                <div style={formDisplay} className="dateFormContainer col-12">
                <form className='addDate col-12' action="" >
                        <div  class="col ">
                          <label for="example-date-input" class="col-2 col-form-label">Date</label>
                          <div class="col">
                              <input class="form-control" type="date" id="example-date-input"/>
                          </div>
                        </div>
                        <div class="col">
                            <label for="inputEmail4" class="form-label">Continent</label>
                            <select class="form-select" aria-label="Default select example">
                              <option selected value="USA">USA</option>
                              <option value="Europe">Europe</option>
                          </select>
                        </div>
                        <div class="col">
                            <label for="inputEmail4" class="form-label">Pays</label>
                            <input type="text" class="form-control" id="inputEmail4"/>
                        </div>
                        <div class="col">
                            <label for="inputEmail4" class="form-label">Ville</label>
                            <input type="text" class="form-control" id="inputEmail4"/>
                        </div>
                        <div class="col">
                            <label for="inputEmail4" class="form-label">Salle</label>
                            <input type="text" class="form-control" id="inputEmail4"/>
                        </div>
                        <div class="col">
                            <div class="pl-4 my-3 form-check form-switch">
                                <label class="form-check-label" for="flexSwitchCheckDefault">Disponible</label>
                                <input class="isAvailable  form-check-input " type="checkbox" id="flexSwitchCheckDefault" defaultChecked="true"/>
                            </div>
                        </div>
                        
                        <div class="col">
                            <label for="inputEmail4" class="form-label">Lien</label>
                            <input type="text" class="form-control" id="inputEmail4"/>
                        </div>
                        <div class="col-12">
                            <button onClick={props.onToggleSubmitDate} type="submit" class="mt-3 btn btn-primary">Ajouter</button>
                        </div>

                  </form>
                </div>
                  
                <table class="table table-striped">
                    <thead>
                      <tr>
                        <th scope="col">#</th>
                        <th scope="col">Date</th>
                        <th scope="col">Continent</th>
                        <th scope="col">Pays</th>
                        <th scope="col">Ville</th>
                        <th scope="col">Salle</th>
                        <th scope="col">Disponibilité</th>
                        <th scope="col">Lien</th>
                        <th scope="col">Paramètres</th>
                      </tr>
                    </thead>
                    <tbody class="table">
                         {dates.map((date, index) => {
                             return <DateTour
                             onToggleUpdateDate={props.onToggleUpdateDate}
                             onToggleDeleteDate={props.onToggleDeleteDate} 
                             key={index}
                             {...date}
                             index={index}
                             />
                         })}
                    
                    </tbody>
                  </table>
                  
                  
            </div>
     );
}
 
export default Dates;