import Experience from "./Experience";

const Experiences = (props) => {
    let experiences = props.experiences;
    return ( 
        <div class="experiences mb-5">
                <h2>Experiences</h2>
                <div class="d-flex col-12 justify-content-between flex-wrap flex-md-nowrap align-items-center pt-3 pb-2 mb-3 border-bottom">
                    <div class="card-group experiencesCards col-12">
                        {experiences.map((experience, index) => {
                            return <Experience key={index} 
                            {...experience}
                            index={index}
                            onToggleDeleteExperience={props.onToggleDeleteExperience}
                            onToggleValidateExperience={props.onToggleValidateExperience}
                            onToggleDeleteExperience={props.onToggleDeleteExperience}
                            />
                        })}
                    </div>
                </div>
        </div>
     );
}
 
export default Experiences;