
const Message = (props) => {
    let mailto = 'mailto:'+ props.email;
    return ( 
        <div className="col-md-6 col-sm-12">
            <div class="card">
            <div class="card-body">
                <h6 class="card-subtitle mb-2 text-muted float-end">{props.date} à {props.hour}</h6>
                <h4 class="card-title">{props.name}</h4>
                <h5 class="card-subtitle mb-2 text-muted">Objet : {props.subject}</h5>
                
                <p class="card-text">{props.message}</p>
                <a href={mailto}
                 class="card-link"><button type='submit' class="btn btn-primary float-end">Répondre</button></a>
                 <button onClick={() => {
                    props.onToggleDeleteMessage(parseInt(props.index))
                }} type='submit' class="btn btn-outline-danger mx-2 float-end">Effacer</button>
            </div>
            </div>
        </div>
     );
}

export default Message;