const Article = (props) => {
    let article = '';
    if (props.isEditing === false) {
        article = (
                    <div class="card">
                        <img src={props.img} class="card-img-top" alt="..."/>
                        <div class="card-body">
                        <h5 class="card-title">{props.title}</h5>
                        <p class="card-text text-muted resume">{props.resume}</p>
                        <p class="card-text text">{props.text}</p>
                        <div className="bottom-card">
                            <p class="card-text"><small class="text-muted">{props.date}</small></p>
                            <p class="card-text">par {props.author}</p>
                            <button onClick={() => {props.onToggleUpdateArticle(props.index)
                            }} type="button" class="fas fa-edit btn btn-dark"></button>
                            <button onClick={() => {props.onToggleDeleteArticle(props.index)
                            }} type="button" class="fa fa-trash btn btn-danger"></button>
                        </div>
                        </div>
                                        
                    </div>
                );
    }else{
        article = (
                    <div class="updateArticleForm card">
                        <img src={props.img} class="card-img-top" alt="..."/>
                        <form class=" col  g-3">
                        <div class="col">
                            <label for="inputEmail4" class="form-label">Titre</label>
                            <input type="text" class="form-control" id="inputEmail4" defaultValue={props.title}/>
                        </div>
                        <div class="col mb-3">
                            <label for="exampleFormControlTextarea1" class="form-label">Resumé</label>
                            <textarea class="form-control" id="exampleFormControlTextarea1" rows="3" defaultValue={props.resume}></textarea>
                        </div>
                        <div class="col mb-3">
                            <label for="exampleFormControlTextarea1" class="form-label">Texte</label>
                            <textarea class="form-control" id="exampleFormControlTextarea1" rows="3" defaultValue={props.text}></textarea>
                        </div>
                        <div class="input-group mb-3">
                            <label class="input-group-text pt-2 fas fa-upload" for="inputGroupFile01"></label>
                            <input onChange={props.uploadFile} type="file" class="form-control" id="inputGroupFile01" required/>
                        </div>
                        <div className="bottom-card">
                        <button onClick={() => {props.onToggleUpdateArticle(props.index)
                        }} type="button" class="fas fa-edit btn btn-dark"></button>
                        <button onClick={() => {props.onToggleDeleteArticle(props.index)
                        }} type="button" class="fa fa-trash btn btn-danger"></button>
                        </div>
                    </form>
                </div>
            );
    }
    return ( 
        <div className="col-md-6 mb-4">
            {article}
        </div>
     );
}
 
export default Article;