import Article from "./Article";
const Articles = (props) => {
    let articles = props.articles;
    let isAddArticle = props.isAddArticle;
    let formDisplay = {};
    if (isAddArticle === false) {
        formDisplay = {
            display: 'none'
        }
    }else{
        formDisplay = {
            display: 'block'
        }
    }
    
    return ( 
        <div class="articles mb-5">
                <button onClick={props.onToggleAddArticle} type='submit' 
                class="my-2 btn btn-primary float-end">{isAddArticle == false ? "Ajouter une publication" : "Fermer"}</button>
                <h2>Publications</h2>
                <div style={formDisplay} className="articleFormContainer col-12">
                <form class=" articleForm col-12  g-3">
                    <div class="col">
                        <label for="inputEmail4" class="form-label">Titre</label>
                        <input type="text" class="form-control" id="inputEmail4"/>
                    </div>
                    <div class="col mb-3">
                        <label for="exampleFormControlTextarea1" class="form-label">Resumé</label>
                        <textarea class="form-control" id="exampleFormControlTextarea1" rows="3"></textarea>
                    </div>
                    <div class="col mb-3">
                        <label for="exampleFormControlTextarea1" class="form-label">Texte</label>
                        <textarea class="form-control" id="exampleFormControlTextarea1" rows="3"></textarea>
                    </div>
                    <div class="input-group mb-3">
                        <label class="input-group-text" for="inputGroupFile01">Image</label>
                        <input onChange={props.uploadFile} type="file" class="form-control" id="inputGroupFile01"/>
                    </div>

                    <div class="col-12">
                        <button onClick={props.onToggleSubmitArticle} type="submit" class="btn btn-primary">Publier</button>
                    </div>
                </form>
                </div>
                
                <div class="d-flex col-12 justify-content-between flex-wrap flex-md-nowrap align-items-center pt-3 pb-2 mb-3 border-bottom">
                    <div class="row card-group articleCards col-12 ">
                        {articles.map((article, index) => {
                            return <Article key={index}
                            onToggleUpdateArticle={props.onToggleUpdateArticle} onToggleDeleteArticle={props.onToggleDeleteArticle}
                            uploadFile={props.uploadFile} index={index} {...article}/>
                        })}
                    </div>

                </div>
        </div>
     );
}
 
export default Articles;