const Admin = (props) => {
    
    let adminInfo = '';
    if (props.isEditing === false) {
        adminInfo = (<tr>
                    <th  scope="row">{props.index+1}</th>
                    <td>{props.name}</td>
                    <td>{props.poste}</td>
                    <td>{props.email}</td>
                    <td>
                    <button onClick={() => {
                        props.onToggleUpdateAdmin(parseInt(props.index));
                    }} type="button" class="fas fa-edit btn btn-outline-dark"></button>
                    <button  onClick={() => {
                        props.onToggleDeleteAdmin(parseInt(props.index));
                    }} type="button" class="fa fa-trash btn btn-outline-danger"></button>
                    </td>
                </tr>);
    }
    if (props.isEditing === true){
        adminInfo = (<tr>
            <th scope="row"></th>
            <td><input type="text" class="name form-control" placeholder="Nom" aria-label="First name" defaultValue={props.name}/></td>
            <td><input type="text" class="poste form-control" placeholder="Poste" aria-label="Last name"  defaultValue={props.poste}/></td>
            <td><input type="text" class="email form-control" placeholder="Email" aria-label="Last name"  defaultValue={props.email}/></td>
            <td>
            <button  onClick={() => {
                props.onToggleUpdateAdmin(parseInt(props.index));
            }} type="button" class="fas fa-edit btn btn-outline-dark"></button>
            <button onClick={() => {
                props.onToggleDeleteAdmin(parseInt(props.index));
            }} type="button" class="fa fa-trash btn btn-outline-danger"></button>
            </td>
        </tr>);
    }
    return ( 
        <>
        {adminInfo}
        </>
     );
}
 
export default Admin;